body {
  font-family: 'Rubik Mono One', sans-serif;
  max-width: 768px;
  margin: 0 auto;
  text-align: center;
  background: #a10000;
  min-height: 160vh; }

h1 {
  color: black;
  display: inline-block;
  font-size: 3.5rem;
  padding: 1rem 2rem;
  background: white;
  border: 5px solid black;
  -webkit-transform: rotate(1deg) translateZ(1px);
          transform: rotate(1deg) translateZ(1px); }

form {
  -webkit-transform: rotate(-2deg) translateZ(1px);
          transform: rotate(-2deg) translateZ(1px); }

.items-container {
  margin-bottom: 8rem; }

.list-item {
  text-align: center;
  border: 5px solid black;
  color: black;
  font-size: 1.6rem;
  padding-top: 1rem;
  background: white; }
  .list-item:first-child {
    margin-top: 3rem; }
  .list-item:nth-child(odd) {
    -webkit-transform: translate(1.5rem) translateZ(1px) rotate(2deg);
            transform: translate(1.5rem) translateZ(1px) rotate(2deg); }
  .list-item:nth-child(even) {
    -webkit-transform: translate(-1.5rem) translateZ(1px) rotate(-1deg);
            transform: translate(-1.5rem) translateZ(1px) rotate(-1deg); }

button {
  cursor: pointer;
  font-family: 'Rubik Mono One', sans-serif;
  display: block;
  width: 100%;
  border: none;
  background: black;
  color: white;
  font-size: 1.6rem;
  padding: 0.5rem;
  margin-top: 1rem; }

input {
  width: 70%;
  border: 5px solid black;
  padding: 1rem;
  font-size: 1.6rem;
  font-family: 'Rubik Mono One', sans-serif; }

.add-btn {
  display: inline-block;
  width: 100px;
  padding: 1rem;
  width: 20%;
  border: 5px solid black; }

.sort-btn {
  margin: 2rem auto 0;
  width: 20%;
  padding: 1.5rem 0;
  display: inline-block; }

@media (max-width: 930px) {
  html {
    font-size: 12px; }
  body {
    width: 70%; } }

@media (max-width: 768px) {
  body {
    width: 80%; } }

@media (max-width: 500px) {
  html {
    font-size: 10px; } }

@media (max-width: 425px) {
  html {
    font-size: 7px; } }

@media (min-width: 940px) {
  html {
    font-size: 14px; } }

